.custom-select {
  position: relative;
  width: 200px;
}

.select-header {
  cursor: pointer;
  padding: 10px;
  border: 1px solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.select-header.open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.arrow {
  margin-left: 10px;
}

.options-list {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
  border: 1px solid #ccc;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #fff;
}

.options-list li {
  cursor: pointer;
  padding: 10px;
  transition: background-color 0.2s;
} 

